@font-face {
    font-family: "sourcecode bold";
    src: url("../fonts/SourceCodePro-Bold.ttf");
}

@font-face {
    font-family: "inter bold";
    src: url("../fonts/Inter-Bold.ttf");
}

@font-face {
    font-family: "Inter Regular";
    src: url("../fonts/Inter-Regular.ttf");
}


body[theme="white"] {
    --background-body: #fff;
    --background-grey: #eceefd;
    --background-header: #f7f8ff;

    --color-font: black;
    --color-border: #e9ebfb;
    --border-table: #dddfef;
}


body[theme="dark"] {
    --background-body: #19191a;
    --background-grey: #2c2c30;
    --background-header: #212122;
    --color-font: rgb(255, 255, 255);
    --color-border: #e9ebfb;
    --border-table: #dddfef14;
}

body {
    background: var(--background-body);
    padding: 0;
    margin: 0;
    height: 100%;
    overflow-x: hidden;
    transition: .5s ease;
    font-family: -apple-system, Segoe UI, Roboto, Ubuntu, Cantarell, Noto Sans, sans-serif, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol" !important;
}





html {
    height: 100%;
}



.controller {
    display: flex;
    width: 100%;
    height: 100vh;
}

.controller .auth {
    display: block;
    width: auto;
    min-width: 400px;
    margin: auto;
    padding: 10px;
    border-radius: 12px;
    background: #22272c;
}

.controller .auth .input-form input {
    display: block;
    width: 100%;
    box-sizing: border-box;
    padding: 12px;
    border-radius: 12px;
    background: transparent;
    border: 1px solid #ffffff30;
    color: white;
    font-size: 14px;
}

.input-form {
    display: block;
    padding: 10px;
    padding-right: 0;
    padding-left: 0;
}

.login {
    display: block;
    width: 100%;
    text-align: center;
    padding: 10px;
    border-radius: 85px;
    background: #398f53;
    border: 0px;
    color: white;
    font-size: 15px;
    margin-top: 10px;
}

.controller .logo {
    display: block;
    max-width: 199px;
    margin: auto;
}

.controller .login-info {
    color: white;
    padding: 10px;
    width: 100%;
    display: block;
    font-size: 14px;
}

.controller .span-info {
    display: block;
    color: white;
    padding: 10px;
    font-size: 13px;
    text-align: center;
    opacity: 0.8;
}

.controller .recovery {
    display: none;
    padding: 10px;
    border-radius: 92px;
    margin-top: 10px;
    width: 100%;
    background: transparent;
    color: white;
    border: 1px solid;
}

.keys {
    margin: auto;
    max-width: 800px;
    color: white;
    height: 100vh;
    margin-top: -17px;
    display: none;
}

.keys .input {
    display: block;
    width: 100%;
    padding: 10px;
    border-radius: 25px;
    background: transparent;
    color: white;
    margin-top: 10px;
    border: 1px solid #ffffff4f;
    font-size: 14px;
}

.row {
    display: flex;
    flex-flow: wrap;
    gap: 10px;
}

.row .col-6 {
    width: 50%;
}

.row .col-4 {
    width: 30.5%;
}

#intro .explication {
    margin-top: 30px;
}

#intro .explication img {
    max-width: 80px;
    margin: auto;
    display: block;
    margin-bottom: 25px;
    object-fit: cover;
}

#intro .explication .title {
    font-size: 20px;
    margin-bottom: 0px;
    padding-bottom: 15px;
}

.keys .span-info {
    display: block;
    color: white;
    font-size: 13px;
    opacity: 0.8;
    justify-content: center;
    padding: 10px 0px 10px 0px;
}

.keys .title {
    font-family: "Sourcecode bold";
    font-size: 17px;
    margin-bottom: 3px;
}

.keys .update {
    display: block;
    width: 100%;
    margin-top: 12px;
    box-sizing: content-box;
    padding: 10px;
    border-radius: 100px;
    background: #a56dff;
    color: white;
    border: 0px;
    font-size: 14px;
    font-family: "Inter bold";
}

.keys .span-info2 {
    font-size: 14px;
    display: block;
    margin-top: 10px;
}

.keys .show-keys {
    display: block;
    position: absolute;
    right: -18px;
    top: -1px;
    bottom: 0;
    margin: auto;
    height: fit-content;
    padding: 7px;
    border-radius: 93px;
    padding-right: 10px;
    padding-left: 10px;
    font-size: 13px;
    background: #404142;
    color: white;
    border: 0px;
}

.keys .input-key {
    display: block;
    position: relative;
}

.keys .center {
    display: block;
    margin: auto;
    animation: showKeys 1s ease;
}

.keys .img-restapi {
    max-width: 135px;
    margin: auto;
    display: block;
    margin-top: 25px;
}

.keys .img-padlock {
    display: block;
    max-width: 154px;
    margin: auto;
    margin-top: 36px;
}


.auth-ok.controller {
    display: none;
}

.auth-ok.keys {
    display: flex;
}

.errores {
    display: none;
    color: white;
    padding: 10px;
    border-radius: 12px;
    background: #d36d6d;
    border: 0px;
    font-size: 14px;
}

@keyframes showKeys {
    0% {
        opacity: 0;
        transform: translateY(30px);
    }

    100% {
        opacity: 1;
    }
}

.wait {
    opacity: 0.5;
    pointer-events: none;
}

.loading {
    display: none;
    position: relative;
    width: 11px;
    height: 11px;
    border: 2px solid white;
    border-radius: 91px;
    border-bottom: 2px solid transparent;
    top: 2px;
    left: 3px;
    animation: circleloading 1s linear infinite;
}

@keyframes circleloading {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

button.wait .loading {
    display: inline-block;
}

button {
    cursor: pointer;
}

.doc .header {
    display: flex;
    width: 100%;
    background: var(--background-header);
    position: sticky;
    top: 0;
    z-index: 99;
    max-width: 100%;
    transition: .5s ease;
}

button,
span,
p,
div,
a,
i {
    transition: .5s ease;
}

.doc .header .search {
    display: block;
    margin: auto;
    width: max-content;
}

.doc .header .search .form-search {
    border-radius: 40px;
    background: var(--background-grey);
    display: flex;
}

.doc .header .search .form-search i {
    display: block;
    padding: 10px;
    padding-left: 11px;
    color: var(--color-font);
    font-weight: 600;
}

.doc .header .search .input-search {
    background: transparent;
    border: 0px;
    outline: none;
    padding: 10px;
    color: var(--color-font);
}

.doc.control {
    width: 100%;
}

.doc .header .logo {
    display: block;
    max-width: 165px;
    padding: 10px;
    max-height: 41px;
    position: relative;
    left: 12px;
}

.doc .header .right {
    display: flex;
    margin-left: auto;
    margin-top: auto;
    margin-bottom: auto;
    margin-right: 15px;
}

.doc .header .right .theme {
    display: block;
    width: 56px;
    height: 27px;
    margin: auto;
    border-radius: 25px;
    position: relative;
    margin-left: 5px;
    background: var(--background-grey);
    border: 1px solid var(--border-table);
    line-height: 0;
    color: var(--color-font);
}

.doc .header .right .theme .dark {
    float: right;
    padding: 3px;
}

.doc .header .right .theme .elegible {
    display: block;
    position: absolute;
    height: 19px;
    border-radius: 92px;
    width: 19px;
    border: 1px solid;
    left: 4px;
    transition: .5s ease;
}

body[theme="dark"] .theme .elegible {
    left: 30px !important;
}

body[theme="white"] .theme .elegible {
    left: 4px !important;
}

.doc .header .right .theme .white {
    float: left;
    padding: 3px;
}

.doc .header .right a {
    display: block;
    padding: 10px;
    margin-right: 5px;
    text-decoration: none;
    background-color: var(--background-grey);
    border-radius: 20px;
    font-size: 14px;
    padding-right: 15px;
    padding-left: 15px;
    color: var(--color-font);
    font-weight: 600;
}

.doc .header .auth-button {
    padding: 10px;
    border-radius: 91px;
    padding-right: 15px;
    padding-left: 15px;
    background: transparent;
    border: 1px solid white;
    color: white;
}

.control .menu {
    display: block;
    width: max-content;
    background: var(--background-body);
    margin-top: 55px;
    z-index: 999;
    height: -webkit-fill-available;
    position: fixed;
    border-right: 1px solid var(--color-border);
    min-width: 295px;
    padding: 10px;
    top: 6px;
    overflow: auto;
    max-height: 100vh;
    padding-bottom: 25px;
    padding-top: 25px;
}



.doc .menu ul {
    margin: auto;
    position: relative;
    padding: 0;
}

.doc .menu .author {
    display: block;
    padding: 10px;
    color: var(--color-font);
    text-align: left;
    border-radius: 6px;
    padding-right: 15px;
    padding-left: 15px;
    border: 0px;
    background: transparent;
    min-width: 210px;
    font-size: 17px;
    opacity: 0.9;
    width: 100%;
    text-decoration: none;
    box-sizing: border-box;
    font-weight: 600;
}

.doc .menu .author:hover {
    background-color: #ffffff0d;
}

.doc .menu .author:hover {
    clip: rect(auto, auto, 30em, auto);
}


.doc .menu ul ul {
    display: block;
    padding-left: 27px;
    list-style: none;
}

.doc .search-hash{
    display: block;
    padding: 20px;
}

.doc .menu ul ul a {
    display: block;
    background: transparent;
    border: 0px;
    color: var(--color-font);
    position: relative;
    width: 92%;
    text-align: left;
    font-size: 14px;
    margin-bottom: -1px;
    padding: 10px;
    text-decoration: none;
}

get,
post-get,
post {
    font-size: 7px;
    padding: 5px;
    border-radius: 4px;
    position: absolute;
    right: 11px;
    font-weight: 600;
    top: 8px;
    bottom: 0;
    height: max-content;

}

post-get {
    background: #51b984;
    color: white;
    border-radius: 93px;
}

.doc .control-i {
    text-align: left;
    max-width: 1600px;
    width: 100%;
    display: flex;
}

.doc .control-i .desc-one {
    color: var(--color-font);
}

.doc .control-i span {
    color: var(--color-font);
}

.doc .control-i .information {
    width: 69vw;
    padding: 20px;
    padding-left: 338px;
    position: relative;
    display: block;
    max-width: 900px;
}

.doc .control-i .code {
    width: 30%;
}


.doc .control {
    display: flex;
}


.doc .information .title {
    font-size: 40px;
    color: var(--color-font);
    font-weight: 600;
    padding: 0px;
    margin: 0;
    padding-bottom: 20px;
}

.doc .information .text-1 {
    padding: 5px;
    color: var(--color-font);
    opacity: 0.8;
}


.doc .information .page {
    display: block;
    animation: openpage 1s ease;
}

.doc .information .page table {
    padding: 10px;
    width: 100%;
    border-collapse: collapse;
    position: relative;
    display: block;
    color: var(--color-font);
}

.doc .information .page table tr {
    border-bottom: 1px solid var(--border-table)
}

.doc .information .page .title-table {
    font-size: 18px;
    font-weight: 500;
}

.doc .information .page table td {
    padding: 15px;
    border-bottom: 1px solid var(--border-table);
}

@keyframes openpage {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

.doc .author.active {
    background-color: #ffffff12;
}

.doc .pre-title {
    font-size: 23px;
    font-weight: 600;
    color: var(--color-font);
}

.doc .information b.important {
    background: #ffffff21;
    padding-right: 15px;
    padding-left: 15px;
    border-radius: 19px;
    font-size: 13px;
    max-width: fit-content;
    display: inline-block;
}

.doc .information b.important a {
    display: block;
    color: white;
}


.doc .download{
    display: block;
    border: 1px solid;
    padding: 10px;
    text-align: center;
    border-radius: 35px;
    text-decoration: none;
    max-width: max-content;
    margin-left: auto;
    background: #51b984;
    color: white;
    font-weight: 600;
    padding-right: 15px;
    padding-left: 15px;
    font-size: 14px;
    margin-top: 10px;
    margin-bottom: 10px;
}

.url-request{
    padding: 13px;
    background: #222226;
    border-radius: 12px;
    color: white !important;
    overflow: auto;
}

.url-request span{
    color: white !important;
    font-weight: 600;
}

.url-request .type{
    font-weight: 600;
    margin-right: 10px;
    color: #82ff82 !important;
}

.doc p.response {
    font-size: 22px;
    font-weight: 600;
    padding: 10px;
    margin-bottom: 0px;
    margin-top: 2px;
    position: relative;
    left: -12px;
    color: var(--color-font);
}

.doc p.params {
    font-size: 22px;
    font-weight: 600;
    color: #4a81ff;
    text-decoration: underline;
}




.doc b.recommend {
    color: #73f2ff;
    text-decoration: underline;
}


.doc b.warning {
    color: rgb(255, 110, 74);
    text-decoration: underline;
}


pre {
    background: rgb(34 34 38) !important;
    padding: 29px !important;
    border-radius: 12px;
    font-size: 14px;
    padding-left: 28px !important;
}

pre span {
    font-weight: 600 !important;
}

.beta {
    padding: 8px;
    background: var(--background-grey);
    max-width: max-content;
    border-radius: 94px;
    padding-right: 15px;
    padding-left: 15px;
    font-size: 11px;
    right: 24px;
    top: 0;
    font-weight: 600;
    bottom: 0;
    color: var(--color-font);
    margin: auto;
    height: max-content;
}

.modal-beta {
    display: block;
    position: fixed;
    z-index: 99;
    background: #00000087;
    top: 0;
    height: 100%;
    backdrop-filter: blur(20px);
    width: 100%;
    display: flex;
}

.modal-beta .body {
    display: block;
    padding: 10px;
    background: #1b1c1e;
    margin: auto;
    max-width: 400px;
    border-radius: 12px;
}

.modal-beta .go {
    display: block;
    width: 100%;
    padding: 10px;
    border-radius: 90px;
    margin-top: 10px;
    border: 0px;
    background: #3c7c48;
    color: white;
    font-size: 14px;
}

.modal-beta span {
    color: white;
    padding: 10px;
    position: relative;
    display: block;
    font-size: 14px;
    opacity: 0.8;
}

.modal-beta img {
    max-width: 172px;
    margin: auto;
    display: block;
    margin-top: 15px;
    margin-bottom: 15px;
}

.modal-beta .return {
    width: 100%;
    display: block;
    padding: 10px;
    text-align: center;
    border: 1px solid;
    box-sizing: border-box;
    margin-top: 10px;
    border-radius: 90px;
    color: white;
    text-decoration: none;
    font-size: 12px;
}

.pc {
    display: block;
}

.mobile {
    display: none;
}

@media only screen and (max-width:900px) {
    .modal-beta .go {
        display: block;
    }
}

.closed {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    background: transparent;
    width: 100%;
    z-index: -900;
}



@media screen and (max-width:900px) {
    .row .col-4 {
        display: block;
        width: 100%;
        margin-bottom: 10px;
    }

    .doc .header {
        height: 57px;
    }

    .pc {
        display: none !important;
    }

    .mobile {
        display: block;
    }

    .control .menu {
        background: var(--color-border) !important;
        position: fixed !important;
        z-index: 9;
        left: -22em;
        transition: .5s ease;
    }

    .header.open~.control .menu {
        left: 0em !important;
        height: 100%;
        background: var(--background-body) !important;
        position: fixed !important;
        max-height: -webkit-fill-available;
        margin-top: 51px;
    }

    .header.open~.control .closed {
        display: block;
    }

    .doc .control-i .information {
        width: 100%;
        box-sizing: border-box;
        padding: 10px;
    }

    .doc .control-i {
        width: 100%;

        overflow: hidden;
    }

    .bar-menu {
        display: block;
        position: absolute;
        bottom: 0;
        top: 0;
        height: max-content;
        margin: auto;
        font-size: 26px;
        line-height: 0.6;
        padding: 10px;
        border-radius: 91px;
        background: transparent;
        color: var(--color-font);
        border: 0px;
    }

    .search .form-search {
        left: -9px;
        position: relative;
        width: 43px;
        overflow: hidden;
    }

    .doc .header .logo {
        display: block;
        max-width: 165px;
        padding: 10px;
        padding-left: 43px;
    }

}